html {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
}

.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  margin: 0;
  width: 100%;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  font-size: 53px;
  line-height: 62px;
}

.page-main__header, .page-main__first-list {
  margin-bottom: 15px;
}

.wrap {
  max-width: 1200px;
  box-sizing: border-box;
  margin: 0 auto;
  background-color: #c0dbd9;
  padding: 0 15px;
}

.header {
  width: 100%;
  padding: 30px;
  background-color: #7faff7;
  box-sizing: border-box;
}

.first__list {
  width: 100%;
  display: grid;
  background-color: #b3f95a;
  grid-template-columns: 2fr 3fr 1fr;
  grid-template-rows: 198px 198px;
  gap: 15px;
  color: #fff;
}
@media (min-width: 1200px) {
  .first__list {
    grid-template-columns: 390px 590px 160px;
  }
}
.first__item {
  display: block;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #8ad191;
}
.first__item:nth-child(4) {
  grid-column: 1/3;
  grid-row: 2/3;
}

.second__list {
  width: 100%;
  display: grid;
  background-color: #88b5bf;
  padding: 30px;
  box-sizing: border-box;
  gap: 30px;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 20%;
}
.second__item {
  width: 100%;
  padding-top: 78%;
  background-color: #0d89b0;
}

.popup {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 400px;
  background-color: #fff;
  display: none;
  transition: all 0.5s;
  font-size: 53px;
  line-height: 62px;
  color: #000000;
}
.popup__btn {
  text-decoration: none;
  color: #fff;
  padding: 8px 15px;
  background: #a5c7f9;
  border-radius: 5px;
}
.popup__btn--close {
  position: absolute;
  top: 15px;
  right: 15px;
  font-weight: 400;
  font-size: 20px;
  line-height: 23px;
}

#popup:target {
  display: flex;
}